body{
  background-color:#fecd0d;
}
.App{
  background-color: #fff;
  min-height:500px ;
  width:400px;
  margin:30px auto;
  border-radius: 10px;

}
.headerName{
text-align: center;
font-weight: 750;
font-size: 38px;
}
#to-do-form input{
  background-color: #f57c24;
  border:0;
  width:200px;
  height:50px;
  padding: 0 20px;
  margin: 20px;
  font-size: 18px;
  border-radius: 10px;
  color:#fff;
}
#to-do-form input::placeholder{
color: rgba(255, 255, 255, 0.5);
}
#to-do-form input:focus,#to-do-form button:focus{
  outline: none;
}

#to-do-form button{
  height: 50px;
  width:80px;
  border-radius: 5px;
  border: 0;
  background-color:#f57c24;
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
}